export class Inventory{
  id: number;
  productId: number;
  unopenedPack: number = null;
  productCode: string;
  productName: string;
  lastCount: Date;
  state: string;
  shouldIncluded: boolean = true;
  shouldCountToday: boolean = true;
  submission_time: Date;
  delivery_submission_time: Date;
  is_delivery_finalised: boolean;
  is_disabled: boolean;
  stockMax: number;
  ref_id: number;
  ref_type_id: number;
}



